@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.c-btn {
  padding: 0.625rem 2.5rem;
  background-color: black;
  color: yellow;
}

.c-test {
  color: red;
  font-size: 1.25rem;
}
@media screen and (min-width: 768px) {
  .c-test {
    font-size: 3.125rem;
    color: green;
  }
}

.l-footer {
  padding: 3rem 0;
}
@media screen and (min-width: 768px) {
  .l-footer {
    padding: 2rem 0 1.625rem 0;
  }
}

.l-inner {
  margin: 0 auto;
  max-width: 33.75rem;
  width: 100%;
  height: inherit;
  padding-right: 20px;
  padding-left: 20px;
}
@media screen and (min-width: 768px) {
  .l-inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.p-card {
  width: 100%;
  max-width: 20.9375rem;
  margin-left: 0.625rem;
}
@media screen and (min-width: 768px) {
  .p-card {
    width: 21.875rem;
  }
}

.p-card:not(:first-child) {
  margin-top: 1.5rem;
}
@media screen and (min-width: 768px) {
  .p-card:not(:first-child) {
    margin-left: 0.625rem;
  }
}

@media screen and (min-width: 768px) {
  .p-card:nth-child(n+4) {
    margin-bottom: 0.625rem;
  }
}

.p-card__img {
  width: 100%;
  position: relative;
}

.p-card__img::before {
  content: "";
  display: block;
  padding-top: 66.567%;
}

.p-card__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.p-card__contents {
  width: 100%;
  padding: 0.25rem 1.4375rem 1rem;
  background-color: #fff;
  display: flex;
  flex-direction: column;
}

.p-card__title {
  font-size: 1.125rem;
  font-weight: 500;
  color: #333;
}

.p-card__text {
  font-size: 0.875rem;
  margin-top: 0.5rem;
}

.p-card__meta {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 0.5rem;
  margin-top: auto;
}

.p-card__category {
  font-size: 0.875rem;
  padding: 0.0625rem 0.25rem;
  background-color: #f9f871;
}

.p-card__date {
  font-size: 0.875rem;
}

.p-card:hover .p-card__contents {
  background-color: #333;
  transition: 0.4s;
}

.p-card:hover .p-card__title,
.p-card:hover .p-card__text,
.p-card:hover .p-card__date {
  color: #fff;
}

.p-card:hover .p-card__img {
  opacity: 0.8;
}

.p-footer {
  background-color: #111;
}

@media screen and (min-width: 768px) {
  .p-footer__contents {
    padding: 0 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.p-footer__logo {
  margin-left: auto;
  margin-right: auto;
  width: 4.375rem;
}
@media screen and (min-width: 768px) {
  .p-footer__logo {
    margin: 0;
  }
}

.p-footer__copyright {
  color: #fff;
  font-size: 0.6875rem;
  letter-spacing: 0.075rem;
  display: block;
  text-align: center;
  margin-top: 2rem;
}
@media screen and (min-width: 768px) {
  .p-footer__copyright {
    font-size: 0.9375rem;
    margin-top: 4.0625rem;
  }
}

.p-test {
  color: red;
  font-size: 1.25rem;
}
@media screen and (min-width: 768px) {
  .p-test {
    font-size: 3.125rem;
    color: green;
  }
}

.rietest {
  background-color: #111;
}